import React from 'react';

class Education extends React.Component{

    constructor(props)
    {
        super(props);
        this.state = {
            educationData : this.props.education
        }
    }
    
    render()
    {
        return(
            <section class="resume-section" id="education">
                <div class="resume-section-content">
                    <h2 class="mb-5">Education</h2>
                    {
                        this.state.educationData.map(edu => {
                            return(
                                <div class="d-flex flex-column flex-md-row justify-content-between mb-5">
                                <div class="flex-grow-1">
                                <h3 class="mb-0">{ this.state.educationData.university } </h3>
                                <div class="subheading mb-3">{ this.state.educationData.degree } </div>
                                <p>GPA: { this.state.educationData.gpa } </p>
                                </div>
                                <div class="flex-shrink-0"><span class="text-primary">{ this.state.educationData.fromDate + ' - '+this.state.educationData.toDate }</span></div>
                                </div>
                            );
                        })
                    }    
                </div>
            </section>
        );
    };
};


export default Education;